import './App.css';
import React from "react";
import {BrowserRouter as Router, Route, Switch} from "react-router-dom";
import Header from "./Components/Header";
import Footer from "./Components/Footer";
import Contacts from "./Templates/Contacts";
import Main from "./Templates/Main";
import ProductCard from "./Templates/ProductCard";
import Delivery from "./Templates/Delivery";
import Burger from "./Components/Burger";

const path = {
  main: '/',
  contacts: '/contacts',
  card: '/card',
  delivery: '/delivery',
}

function App() {

  return (
    <div className="App">
      <Router>
        <Burger path={path}/>
        <div className="content">
          <Header path={path}/>
          <main className='root'>
            <Switch>
              <Route path={path.main} exact>
                <Main/>
              </Route>
              <Route path={path.contacts}>
                <Contacts/>
              </Route>
              <Route path={path.card}>
                <ProductCard/>
              </Route>
              <Route path={path.delivery}>
                <Delivery/>
              </Route>
            </Switch>
          </main>
          <Footer/>
        </div>
      </Router>
    </div>

  );
}

export default App;
