import React, {useState} from "react";
import './index.css'
import Card from "../../Components/Card";
import Modal from "../../Components/Modal";
import base from './../../base_flowers.json'


function Main() {
  const [visibleModal, setVisibleModal] = useState(false)
  const [dataModal, setDataModal] = useState({
    title: '',
    price: '',
    description: '',
  })
  const [data, setData] = useState({})

  fetch("http://romashka-spb.ru/static/base_flowers.json")
    .then(response => response.json())
    .then(json => setData(prev => {
      return {
        ...prev,
        ...json
      }
    }))
    .catch(error => console.log(error))

  const arr = Object.values(data).map(({description, id, name, price, image}, i) => <Card
    data-id={id}
    key={id}
    dataCard={{
      title: name,
      price,
      description,
      image,
    }}
    setDataModal={setDataModal}
    setVisible={setVisibleModal}/>)

  const modalTemplate = (
    <div className="modal__content">
      <h2 className="modal__title">{dataModal.title}</h2>
      <p className="modal__text modal__text_type_underlined modal__text_theme_shadow">{dataModal.description}</p>
      <span className="modal__text modal__text_theme_shadow">Цена: {dataModal.price} ₽</span>
    </div>)

  return (
    <section className="main">
      <div className="card-container">
        {arr}
      </div>
      <Modal
        template={modalTemplate}
        setVisible={setVisibleModal}
        visible={visibleModal}/>
    </section>
  )
}

export default Main
