import React from "react";
import cx from 'classnames';
import './index.css'

function Modal(props) {
  const {visible, setVisible, template} = props

  function toggleModal(e) {
    if (e.target.classList.contains('modal') || e.target.classList.contains('button')) {
      setVisible(prev => !prev)
    }
  }

  return (
    <div onClick={(e) => toggleModal(e)} className={cx('modal', {'modal_open': visible})}>
      <div className={cx('modal__content-container', {'modal__content-container_visible': visible})}>
        {template}
        <div className="modal__button-container">
          <button
            className="button button_size_small button_theme_flower modal__button modal__button-accept">Закрыть
          </button>
        </div>
      </div>
    </div>
  )
}

export default Modal
