import React, {useState} from "react";
import './index.scss'
import LinkItem from "../Link";
import {elastic as Menu} from "react-burger-menu";

function Burger(props) {
  const {main, contacts, delivery} = props.path
  const [isOpen, setIsOpen] = useState(false)

  const onClose = () => {
    setIsOpen(prev => !prev)
  }

  const isMenuOpen = (state) => {
    setIsOpen(state.isOpen)
  };

  const createLinks = () => {
    const classNameLink = ['burger__link', 'bm-item'];
    return [
      {key: main, title: 'Главная', path: main, classNameLink},
      {key: delivery, title: 'Доставка/Оплата', path: delivery, classNameLink},
      {key: contacts, title: 'Контакты', path: contacts, classNameLink},
    ]
  }
  const dataLinks = createLinks()

  const linksElements = dataLinks.map(({key, title, path, classNameLink}) =>
    <LinkItem
      key={key}
      classNameLink={classNameLink}
      title={title}
      path={path}
      eventClick={onClose}
    />)
  return (
    <Menu
      isOpen={isOpen}
      onStateChange={isMenuOpen}
      width={400}
      right>
      {linksElements}
    </Menu>
  )
}

export default Burger
