import React from "react";
import './index.scss'
import classNames from 'classnames'
import {Link} from "react-router-dom";

function LinkItem(props) {
  const {
    title, classNameLink = '', path, eventClick = () => {
    }
  } = props
  return (
    <Link onClick={() => eventClick()} className={classNames('link', [...classNameLink])} to={path}>{title}</Link>
  )

}

export default LinkItem
