import React from "react";
import {Link} from "react-router-dom";
import './index.scss'
import LinkItem from "../Link";


function Header(props) {
  const {main, contacts, delivery} = props.path

  const createLinks = () => {
    const classNameLink = 'header__link';
    return [
      {key: main, title: 'Главная', path: main, classNameLink: [classNameLink]},
      {key: delivery, title: 'Доставка/Оплата', path: delivery, classNameLink: [classNameLink]},
      {key: contacts, title: 'Контакты', path: contacts, classNameLink: [classNameLink]},
    ]
  }
  const dataLinks = createLinks()
  const linksElements = dataLinks.map(({key, title, path, classNameLink}) =>
    <LinkItem
      key={key}
      classNameLink={classNameLink}
      title={title}
      path={path}
    />)

  return (
    <header
      className='header'
    >
      <Link to={main}>
        <img className='header__logo'
             src="/static/icons/logo.png"
             alt="логотип"/>
      </Link>
      <div className="header__info">
        <a href="tel:+78129221742" className="link header__phone">+7 (812) 922-17-42</a>
        <span className="header__schedule">Работаем для вас 24/7</span>
      </div>

      <nav className='header__nav'>
        <div className='header__link-container'>
          {linksElements}
        </div>
      </nav>

    </header>
  )
}

export default Header
