import React from "react";
//import {useHistory} from "react-router-dom";
import './index.scss'
import Button from "../Button";


function Card(props) {
  const {setVisible, setDataModal, dataCard} = props

  //const history = useHistory();
  //const navigateTo = (path) => history.push(`/${path}`);

  function toggleModal() {
    setDataModal(prev => {
      return {
        ...prev,
        ...dataCard,
      }
    })
    setVisible(prev => !prev)
    //navigateTo('card')
  }

  return (
    <div className="card" onClick={() => toggleModal(prev => !prev)}>
      <div style={{backgroundImage: 'url(' + dataCard.image + ')'}} className='card__img'/>
      <div className="card__info">
        <h2 className='title card__title'>{dataCard.title}</h2>
        <span className='card__price'>Цена: {dataCard.price} ₽</span>
      </div>
      <div className="card__button-container">
        <Button title="Купить в 1 клик"
                className="button button_size_small  button_type_border-radius button_theme_flower card__button"/>
        <Button title="$"
                className="button button_size_small button_type_border-radius button_theme_flower-transparent card__button"/>
      </div>
    </div>
  )
}

export default Card
